import FlexibleDescription from "../vendor/flexible-description.min";
export default class Lists {
  constructor() {
    this.init();
  }
  init() {
    this.default_list_rows();
    this.has_attrs_list_rows();
  }

  default_list_rows() {
    new FlexibleDescription({
      selector: ".list-card:not(.has-attrs)",
      rows: 3,
      titleSelector: ".card-title>p>a",
      descriptionSelector: ".card-title>.desc",
      responsive: [
        {
          breakpoint: 1248,
          rows: 5,
        },
        {
          breakpoint: 768,
          rows: 4,
        },
      ],
    });
  }

  has_attrs_list_rows() {
    new FlexibleDescription({
      selector: ".list-card.has-attrs",
      rows: 3,
      titleSelector: ".card-title>p>a",
      descriptionSelector: ".card-title>.desc",
      responsive: [
        {
          breakpoint: 1248,
          rows: 4,
        },
        {
          breakpoint: 768,
          rows: 3,
        },
      ],
    });
  }
}
