export default class Banner {
  constructor() {
    this.init();
  }

  init() {
    this.banner1_banner2_slick();
    this.banner3_slick();
    this.banner5_slick();
  }

  banner1_banner2_slick() {
    let $bannerCardSlick = $(".banner-carousel-1, .banner-carousel-2");
    let options = {
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: false,
      autoplay: true,
      autoplaySpeed: 5000,
      speed: 300,
      dots: true,
      arrows: true,
      rows: 0,
      draggable: true,
      pauseOnFocus: false,
      fade: false,
      cssEase: "linear",
    };

    $bannerCardSlick.slick(options);
    $bannerCardSlick.on("beforeChange", function () {
      window.LazyLoad.update();
    });
  }

  banner3_slick() {
    let $bannerCardSlick = $(".banner-carousel-3");
    let $largetItem = $(".large-ban .ban-item").clone().addClass("md-hide");

    let options = {
      infinite: true,
      slidesToShow: 1,
      slidesToScroll: 1,
      adaptiveHeight: false,
      dots: true,
      arrows: false,
      rows: 0,
      draggable: true,
      pauseOnFocus: false,
      fade: false,
      cssEase: "linear",
      responsive: [
        {
          breakpoint: 10000,
          settings: "unslick",
        },
        {
          breakpoint: 767,
          settings: {
            autoplay: true,
            autoplaySpeed: 5000,
            speed: 300,
          },
        },
      ],
    };

    $bannerCardSlick.prepend($largetItem).slick(options);
    $bannerCardSlick.on("beforeChange", function () {
      window.LazyLoad.update();
    });

    this.slick_revival($bannerCardSlick, options);
  }

  banner5_slick() {
    let $bannerCardSlick = $(".banner-carousel-5");
    // let options = {
    //   infinite: true,
    //   slidesToShow: 3,
    //   slidesToScroll: 1,
    //   adaptiveHeight: false,
    //   autoplay: false,
    //   autoplaySpeed: 5000,
    //   speed: 300,
    //   dots: false,
    //   arrows: false,
    //   rows: 0,
    //   draggable: true,
    //   pauseOnFocus: false,
    //   cssEase: "linear",
    //   centerMode: true,
    //   centerPadding: "0",

    //   responsive: [
    //     {
    //       breakpoint: 1199,
    //       settings: {
    //         centerMode: false,
    //         variableWidth: false,
    //         speed: 300,
    //       },
    //     },
    //     {
    //       breakpoint: 767,
    //       settings: {
    //         slidesToShow: 1,
    //         centerMode: false,
    //         variableWidth: false,
    //         speed: 260,
    //         dots: true,
    //       },
    //     },
    //   ],
    // };

    // $bannerCardSlick.slick(options).removeClass("init");
    // $bannerCardSlick.on("beforeChange", function () {
    //   window.LazyLoad.update();
    // });

    $.getScript(
      "https://cdnjs.cloudflare.com/ajax/libs/Swiper/6.4.1/swiper-bundle.min.js",
      () => {
        let swiper = new Swiper(".swiper-container", {
          slidesPerView: 1,
          spaceBetween: 0,
          centeredSlides: true,
          loop: true,
          disableOnInteraction: true,
          coverflowEffect: {
            rotate: 0,
            slideShadows: false,
          },
          autoplay: {
            delay: 5000,
          },
          pagination: {
            el: ".swiper-pagination",
            clickable: true,
          },
          navigation: {
            nextEl: ".swiper-container .slick-next",
            prevEl: ".swiper-container .slick-prev",
          },
          breakpoints: {
            768: {
              slidesPerView: 2,
              spaceBetween: 46,
            },
            992: {
              slidesPerView: 2,
              spaceBetween: 56,
            },
            1248: {
              slidesPerView: 3,
              spaceBetween: 56,
            },
          },
        });

        setTimeout(() => {
          $bannerCardSlick.removeClass("init");
          //  
          let $img = $(".swiper-slide-prev,.swiper-slide-next").find(
            "img.lazy:not(.loaded)"
          );

          $img.map((i, el) => {
            if ($(el).attr("data-src")) {
              $(el).attr("src", $(el).data("src")).addClass("loaded");
            }
          });
        }, 120);

        this.user_interaction(swiper);
      }
    );
  }

  user_interaction(swiper) {
    //  
    $(".swiper-container").mouseenter(() => {
      swiper.autoplay.stop();
    });

    $(".swiper-container").mouseleave(() => {
      swiper.autoplay.start();
    });

    //  
    $(".swiper-container .slick-arrow").on("click", () => {
      swiper.autoplay.start();
    });
  }

  slick_revival($mySlick, options) {
    $mySlick.on("destroy", (ev, slick) => {
      ev.currentTarget.dataset.slickInit = false;
    });

    $mySlick.map((k, item) => {
      item.dataset.slickInit = false;
    });

    $(window).on("resize orientationchange", () => {
      if (window.innerWidth < 767) {
        $mySlick.map((k, el) => {
          if (
            !~el.className.indexOf("slick-initialized") &&
            el.dataset.slickInit == "false"
          ) {
            $(el).slick(options);
            el.dataset.slickInit = true;
          }
        });
      }
    });
  }
}
