//  
const get_style = (ele, attr) => {
  return (
    ele &&
    (ele.currentStyle ? ele.currentStyle[attr] : getComputedStyle(ele)[attr])
  );
};

export default class PopularAd {
  constructor() {
    this.articleBgType = $(".popular-article-bg");
    this.popTitle = this.articleBgType.find(".pop-article-title");
    this.popDesc = this.articleBgType.find(".pop-article-desc");

    this.init();
  }

  init() {
    this.desc_limit_line();
  }

  desc_limit_line() {
    let limit_line = () => {
      let titleHeight = this.popTitle.outerHeight();
      let titleFontSize = parseInt(
        get_style(this.popTitle.get(0), "lineHeight")
      );
      let lint = titleHeight / titleFontSize;

      if (lint > 2) {
        this.popDesc.hide();
      } else if (lint === 2) {
        this.popDesc.attr("style", "-webkit-line-clamp: 1; max-height: 1.5em;");
      } else {
        this.popDesc.show().removeAttr("style");
      }
    };

    limit_line();

    $(window).on("resize", this.debounce(limit_line, 200));
  }

  debounce(fn, delay) {
    delay = delay || 100;
    return () => {
      let args = Array.prototype.slice.apply(arguments);
      clearTimeout(fn.debounceTimeout);
      fn.debounceTimeout = setTimeout(() => {
        fn.apply(window, args);
      }, delay);
    };
  }
}
