export default class CommercialFn {
  constructor() {
    this.$asideCommercialWrap = $(".aside-commercial-wrap");
    this.init();
  }
  init() {
    this.close_commercial();
  }
  close_commercial() {
    this.$asideCommercialWrap.on("click", ".close-btn", (ev) => {
      let $parents = $(ev.currentTarget).closest(".aside-commercial-wrap");
      $parents.animate(
        {
          height: 0,
          opacity: 0,
        },
        {
          duration: 240,
          complete: () => {
            $parents.remove();
          },
        }
      );
    });
  }
}
